.single-post {
  .site-content {
    display: flex;
    margin: auto;
    width: 1120px;
    .content-area {
      border-right: 1px solid #e1e1e1;
      padding-right: 5%;
      width: 73%;
      padding-top: 99px;

      article {
        .entry-header {
          position: relative;
          overflow: hidden;

          .entry-title {
            background-color: #248a83;
            font-family: 'novecento-bold';
            color: #fff;
            font-size: 2.1rem;
            left: 0;
            padding: 0.75rem 1.5rem;
            position: absolute;
            margin-top: 2.25rem;
            max-width: 75%;
            top: 0;
            text-transform: uppercase;
            line-height: 1.25;
          }
          .entry-meta {
            background-color: #248a83;
            bottom: 7px;
            color: white;
            display: inline-block;
            font-family: 'novecento';
            padding: 0.2rem 1.5rem;
            position: absolute;
            right: 0;
            text-transform: uppercase;
          }
        }
        .entry-content {
          p {
            margin-bottom: 1.5rem;
            color: #404040;
            font-family: 'Merriweather-light';
            font-size: 1rem;
            line-height: 1.75;
            .read-more {
              display: none;
            }
          }
        }
      }
      .social-links {
        // font-size: 0.75rem;
        // line-height: 1;
        // padding: 0.6rem 1rem 0.4rem;
        // border-radius: 0;
        // background: none;
        button {
          background-color: transparent;
          border-radius: 0;
          border: 1px solid #000;
          color: #000;
          font-family: 'novecento';
          font-size: 0.9rem;
          line-height: 1;
          padding: 0.75rem 1.5rem;
          text-decoration: none;
          text-transform: uppercase;
        }
      }
    }
  }
}
